










import { defineComponent, onMounted } from '@vue/composition-api';
import { vibrate } from '@/use/common';
export default defineComponent({
  name: 'TheAlert',
  props: {
    alertType: {
      type: String,
      required: true
    },
    alertText: {
      type: String,
      required: true
    }
  },
  setup(_, { root: { $store }, emit }) {
    onMounted(() => vibrate());

    const setError = () => $store.dispatch('setError');

    const onClose = () => {
      setError();
      emit('dismissed');
    };

    return {
      onClose
    };
  }
});
